import React from "react";
import { connect } from "react-redux";
import * as JobDataActions from "../../store/JobData/JobDataAction";
import { bindActionCreators } from "redux";
import CardContent from "@material-ui/core/CardContent";
import Button from "../../components/Controls/Button/Button";
import CustomizedInputs from "../../components/Atoms/CustomizedInputs/CustomizedInputs";
import "./AddJobtitle.css";
import { withRouter } from "react-router-dom";
import TableComponent from "../../components/Controls/Table/sortableTable";
import ReactGA from "react-ga";

class AddJobtitle extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      jobtitle: "",
      jobErr: "",
    };
  }
  componentDidMount() {
    ReactGA.pageview(window.location.pathname);
    this.props.JobDataAction.getJobData();
  }
  componentDidUpdate(prevProps) {
    const prev = prevProps && prevProps.JobDataReducer;
    const cur = this.props && this.props.JobDataReducer;
    if (prev.isJobDataAdded !== cur.isJobDataAdded) {
      this.props.JobDataAction.getJobData();
    }
  }

  handleChange = (e) => {
    this.setState({ jobtitle: e.target.value });
  };
  valiDateFields = () => {
    let validate = true;

    if (this.state.jobtitle === "") {
      validate = false;
      this.setState({ jobErr: "Please enter job title" });
    } else {
      this.setState({ jobErr: "" });
    }

    return validate;
  };
  handleAdd = () => {
    if (this.valiDateFields()) {
      const candid = localStorage.getItem("id");
      const candemail = localStorage.getItem("email");
      const uniqueID = candemail + candid;

      const jobtitle = this.state.jobtitle;

      const payload = {
        uniqueId: uniqueID,
        userID: candid,
        jobtitle: jobtitle,
      };
      this.props.JobDataAction.addJobData(payload);
      this.setState({ jobtitle: "" });
    } else {
    }
  };

  render() {
    let jobtitle = [];
    if (
      this.props &&
      this.props.JobDataReducer &&
      this.props.JobDataReducer.getJobData &&
      this.props.JobDataReducer.getJobData.data &&
      this.props.JobDataReducer.getJobData.data.jobTitles
    ) {
      this.props.JobDataReducer.getJobData.data.jobTitles.map((item) => {
        jobtitle.push({
          id: item._id,
          jobtitle: item.jobtitle,
          startdate: new Date(item.createdAt)
            .toString()
            .replace(/\S+\s(\S+)\s(\d+)\s(\d+)\s.*/, "$2-$1-$3"),
        });
      });
    }

    let inboundDatacolumn = [
      {
        Header: "Job title",
        accessor: "jobtitle",
      },

      {
        Header: "CreatedAt",
        accessor: "startdate",
      },
    ];
    return (
      <div>
        <div className='resume-card'>
          <div className='res-card1'>
            <CardContent className='res-card-content'>
              <p className='res-head'>Add Job Title</p>
              <div className='res-wrap'>
                <div className='res-inform'>
                  <div>
                    <div className='res-details-wrap'>
                      <div className='res-details-wrap1'>
                        <CustomizedInputs
                          placeholder='Enter Job Title'
                          value={this.state.jobtitle}
                          onChange={this.handleChange}
                        />
                        {!this.state.jobtitle && (
                          <p className='errorMsg errorHireWidth'>
                            {this.state.jobErr}
                          </p>
                        )}
                      </div>

                      <Button
                        width='140px'
                        height='47px'
                        borderRadius='66px'
                        marginTop='10px'
                        className='res-btn'
                        onClick={this.handleAdd}
                      >
                        Add Job Title
                      </Button>
                    </div>
                  </div>
                </div>
              </div>
            </CardContent>
          </div>
        </div>
        <div className='container'>
          <div className='table-left-space'>
            <div className='px-4'>
              <h4>Jobtitle List</h4>
            </div>
            <TableComponent
              className={"jd-table"}
              data={jobtitle}
              columns={inboundDatacolumn}
              sortable={true}
            />
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    JobDataReducer: state.JobDataReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    JobDataAction: bindActionCreators(JobDataActions, dispatch),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(AddJobtitle));
