import React, { Component } from "react";
import ReactTable from "react-table";
import "./sortable.scss";
import "react-table/react-table.css";
import ArrowLeftIcon from "@material-ui/icons/ArrowLeft";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import RefreshIcon from "@mui/icons-material/Refresh";
import ErrorOutlineIcon from "@mui/icons-material/ErrorOutline";
import SelectInput from "../../Atoms/SelectInput/SelectInput";
import withFixedColumns from 'react-table-hoc-fixed-columns';
import 'react-table-hoc-fixed-columns/lib/styles.css';
const ReactTableFixedColumns = withFixedColumns(ReactTable);
export default class TableComponent extends Component {
  state={
    pageNumberNew:1
  }

  componentDidMount(){
    this.setState({
      pageNumberNew:this.props.pageNumber ? this.props.pageNumber : 1
    })
  }

  render() {
    const {
      pageSize,
      setPageSize,
      gotoPage,
      totalPage,
      pageNumber,
      handlePrev,
      handleNext,
    } = this.props;
    return (
      <div className={`white-table ${this.props.className}`}>
        {this.props.showPaginationTop ? (
          <div className='paginationTopContainer'>
            <div className='options-wrapper'>
              {this.props.selectedRows && this.props.selectedRows > 0 ? (
                <div style={{ display: "flex" }}>
                  <div style={{ display: "flex" }}>
                    {this.props.paginationButtons.map((item) => item)}
                  </div>
                  {this.props.message ? (
                    <div className='selected-items-heading'>
                      <ErrorOutlineIcon
                        style={{
                          marginTop: "-3px",
                          fontSize: "medium",
                          marginRight: "5px",
                        }}
                      />
                      {this.props.message}
                    </div>
                  ) : null}
                </div>
              ) : this.props.handleRefresh ? (
                <div
                  className='refreshIcon-container'
                  onClick={this.props.handleRefresh}
                >
                  <RefreshIcon style={{ color: "#21745D", width: "25px" }} />
                </div>
              ) : null}
            </div>
            <div className='pagination-wrapper'>
              {this.props.hideRowsDropdown ? null : (
                <span className='shows-rows'>Show Rows</span>
              )}
              {this.props.hideRowsDropdown ? null : (
                // <select
                //   className="range"
                //   value={pageSize}
                //   onChange={(e) => setPageSize(Number(e.target.value))}
                // >
                //   {[10, 20, 50, 100].map((size) => (
                //     <option key={size} value={size}>
                //       {size}
                //     </option>
                //   ))}
                // </select>
                <SelectInput
                  className={"selectRowCountDropDown"}
                  onChange={(e) => setPageSize(Number(e.value))}
                  options={[
                    { label: 10, value: 10 },
                    { label: 20, value: 20 },
                    { label: 50, value: 50 },
                    { label: 100, value: 100 },
                  ]}
                  value={{ label: pageSize, value: pageSize }}
                  placeholder=''
                />
              )}
              <span className='shows-rows' style={{ marginLeft: "6px" }}>
                <span style={{ marginLeft: "6px", marginRight: "6px" }}>
                  Go to page:
                </span>
                <input
                  className='range-box'
                  type='number'
                  min={1}
                  max={totalPage}
                  // defaultValue={pageNumber}
                  onChange={(e) => {
                    const pageNumber =
                      e.target.value === ""
                        ? 1
                        : e.target.value <= totalPage
                        ? parseInt(e.target.value)
                        : 1;
                    // gotoPage(pageNumber);
                    this.setState({
                      pageNumberNew:pageNumber
                    })
                  }}
                  style={{ width: "40px" }}
                  value={this.state.pageNumberNew}
                  name='page'
                />
                <span className='goBtn' onClick={() => gotoPage(this.state.pageNumberNew)}>
                  GO
                </span>
              </span>{" "}
              <span className='shows-rows'>
                {pageNumber} of {totalPage}
              </span>
              <span
                className={pageNumber == 1 ? "disable-btn" : "arrow-btn"}
                style={{ margin: "0px 6px" }}
                onClick={() => (pageNumber <= 1 ? {} : handlePrev())}
              >
                <ArrowLeftIcon style={{ color: "#fff" }} />
              </span>
              <span
                className={
                  pageNumber >= totalPage ? "disable-btn" : "arrow-btn"
                }
                onClick={() => (pageNumber >= totalPage ? {} : handleNext())}
              >
                <ArrowRightIcon style={{ color: "#fff" }} />
              </span>
            </div>
          </div>
        ) : null}
        <ReactTableFixedColumns
          style={{
            width: this.props.width ? this.props.width : "",
            justifyContent: this.props.justifyContent
              ? this.props.justifyContent
              : "",
          }}
          display={this.props.display}
          data={this.props.data}
          columns={this.props.columns}
          width={this.props.width}
          // sortable={this.props.sortable} 
          sortable={false}
          filterable={this.props.filterable}
          filter={this.props.Filter}
          resizable={true}
          showPagination={this.props.nopagination ? false : true}
          // PaginationComponent={Pagination}
          showPageSizeOptions={true}
          defaultPageSize={pageSize ? pageSize : this.props.data.length}
          pageSize={pageSize ? pageSize : this.props.data.length}
          noDataText={"No data found"}
          className="-striped -highlight"
          justifyContent={this.props.justifyContent}
          defaultPage={this.props.pageNum ? this.props.pageNum : 1}
          getTdProps={(a, b, { fixed }) => { return ({ style: { background: fixed ? '#fff' : 'inherit' } }); }}
          getTrProps={(state, rowInfo, instance) => {
            return {
              style: {
                backgroundColor: this.props.rowColor
                  ? this.props.rowColor
                  : rowInfo &&
                    rowInfo.original &&
                    rowInfo.original.isSeen === false
                  ? "#CADEDBC7"
                  : rowInfo &&
                    rowInfo.original &&
                    rowInfo.original.isSeen === true
                  ? "#FFFFFF" 
                  : rowInfo && rowInfo.original && rowInfo.original.isError
                  ? "#ff000040"
                  : "",
                fontWeight: this.props.fontWeight
                  ? this.props.fontWeight
                  : (rowInfo &&
                      rowInfo.original &&
                      rowInfo.original.isSeen === false) ||
                    (rowInfo && rowInfo.original && rowInfo.original.isError)
                  ? "700"
                  : "",
              },
            };
          }}
          handlePageClick={this.props.handlePageClick}
          totalPage={
            this.props.totalPage === undefined ? 2 : this.props.totalPage
          }
          showPaginationBottom={
            this.props.showBottomPagination == false ? false : true
          }
        />

        {!this.props.nopagination ? (
          <div className='pagination-wrapper'>
            <span className='shows-rows'>Show Rows</span>
            {/* <select
              className="range"
              value={pageSize}
              onChange={(e) => setPageSize(Number(e.target.value))}
            >
              {[10, 20, 50, 100].map((size) => (
                <option key={size} value={size}>
                  {size}
                </option>
              ))}
            </select> */}
            <SelectInput
              className={"selectRowCountDropDown"}
              onChange={(e) => setPageSize(Number(e.value))}
              options={[
                { label: 10, value: 10 },
                { label: 20, value: 20 },
                { label: 50, value: 50 },
                { label: 100, value: 100 },
              ]}
              value={{ label: pageSize, value: pageSize }}
              placeholder=''
              // placement="top"
            />
            <span className='shows-rows' style={{ marginLeft: "6px" }}>
              <span style={{ marginLeft: "6px", marginRight: "6px" }}>
                Go to page:
              </span>
              <input
                className='range-box'
                type='number'
                min={1}
                max={totalPage}
                value={this.state.pageNumberNew}
                onChange={(e) => {
                  const pageNumber =
                    e.target.value == ""
                      ? 1
                      : e.target.value <= totalPage
                      ? parseInt(e.target.value)
                      : 1;
                  // gotoPage(pageNumber);
                  this.setState({
                    pageNumberNew : pageNumber
                  })
                }}
                style={{ width: "40px" }}
                // value={this.state.pageNumberNew}
                name='page'
              />
              <span className='goBtn' onClick={() => gotoPage(this.state.pageNumberNew)}>
                GO
              </span>
            </span>{" "}
            <span className='shows-rows'>
              {pageNumber} of {totalPage}
            </span>
            <span
              className={pageNumber == 1 ? "disable-btn" : "arrow-btn"}
              style={{ margin: "0px 6px" }}
              onClick={() => (pageNumber <= 1 ? {} : handlePrev())}
            >
              <ArrowLeftIcon style={{ color: "#fff" }} />
            </span>
            <span
              className={pageNumber >= totalPage ? "disable-btn" : "arrow-btn"}
              onClick={() => (pageNumber >= totalPage ? {} : handleNext())}
            >
              <ArrowRightIcon style={{ color: "#fff" }} />
            </span>
          </div>
        ) : null}
      </div>
    );
  }
}
